// -----------------------------------------------------------------------------
// IG POST LAYOUT PRESETS (the real ‚Äúpost design ideas‚Äù)
// -----------------------------------------------------------------------------
export type LayoutPreset = {
  id: string;
  label: string;

  // layout skeleton
  heroAlignment: "left" | "center" | "right";
  composition: "centered" | "asymmetric" | "split" | "sticker" | "badge" | "story";

  // decorative rules (your renderer can interpret these)
  hasPattern: boolean;
  patternKind?: "dots" | "grid" | "waves" | "stars" | "confetti" | "scribble";
  hasBlob: boolean;
  hasStickers: boolean;

  // typography density
  titleScale: "xl" | "2xl" | "3xl";
  subtitleMaxLines: 2 | 3 | 4;

  // phone mock placement idea (if you render screenshots)
  phoneHint?: {
    position: "left" | "right" | "center";
    rotationDeg: number;
    scale: number;
  };

  // CTA style hint
  ctaStyle: "pill" | "button" | "underline" | "none";
};

