// -----------------------------------------------------------------------------
// SLIDE MODEL (expanded for IG)
// -----------------------------------------------------------------------------
export type SlideSticker = {
  id: string;
  text: string;
  variant: "badge" | "label" | "chip";
  position: { x: number; y: number }; // percentage-like coordinates for your renderer
  rotation?: number;
};

export type SlideContent = {
  id: number;

  // content
  title: string;
  subtitle: string;
  seriesTag?: string; // e.g. "KidsAI ‚Ä¢ New"
  ctaText?: string;

  // theming
  color: ThemeColor;

  // choose a post layout
  preset?: keyof typeof layoutPresets;

  // optional store app store title/subtitle
  storeTitle?: string;
  storeSubtitle?: string;

  // phone screenshot rendering options
  phoneStyle?: "rounded" | "iphone" | "android" | "none";
  phoneRotation?: number;
  phoneScale?: number;
  phonePosition?: { x: number; y: number };
  imageFit?: "cover" | "contain" | "fill";
  imagePosition?: { x: number; y: number };
  imageZoom?: number;
  imageRotation?: number;

  // extra IG elements
  stickers?: SlideSticker[];
  footerNote?: string; // e.g. "3‚Äì6 ya≈ü i√ßin"
  highlightWords?: string[]; // renderer can bold/underline these words
  safeArea?: { top: number; right: number; bottom: number; left: number }; // px-like hint
};

// -----------------------------------------------------------------------------
// LAYOUT CONFIG (carousel sets)
// -----------------------------------------------------------------------------
export type LayoutConfig = {
  name: string;
  slides: SlideContent[];
};

